:root {
  --accent: #54be71;
  --imgHeight: 100vh;
}

* {
  padding: 0;
  margin: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  position: relative;
}

html {
  background-color: #1c1d1d;
  color: #fbfbfb;
  font: 87.5%/1.5 system-ui, -apple-system, "BlinkMacSystemFont", "SF UI",
    "Helvetica Neue", sans-serif;
  height: 100%;
  overflow: hidden;
}

a {
  color: inherit;
  -webkit-text-decoration-color: rgba(255, 255, 255, 0.5);
          text-decoration-color: rgba(255, 255, 255, 0.5);
  -webkit-text-decoration-skip: ink;
          text-decoration-skip: ink;
  -webkit-transition: 0.2s ease;
  -o-transition: 0.2s ease;
  transition: 0.2s ease;
  -webkit-transition-property: color, -webkit-text-decoration-color;
  transition-property: color, -webkit-text-decoration-color;
  -o-transition-property: color, text-decoration-color;
  transition-property: color, text-decoration-color;
  transition-property: color, text-decoration-color, -webkit-text-decoration-color;
}

a:hover,
a:focus {
  color: var(--accent);
  -webkit-text-decoration-color: currentColor;
          text-decoration-color: currentColor;
}

p {
  margin-bottom: 1.5rem;
}

.frac {
  -webkit-font-feature-settings: "frac" 1;
          font-feature-settings: "frac" 1;
}

.caps {
  -webkit-font-feature-settings: "smcp" 1, "c2sc" 1;
          font-feature-settings: "smcp" 1, "c2sc" 1;
}

.site-title {
  font-weight: 400;
  font-size: 1rem;
}

.site-root {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column nowrap;
      flex-flow: column nowrap;
  height: 100%;
