    min-width: 30rem;
  }
}

.pane__image {
  background-color: rgba(0, 0, 0, 0.5);
  margin-bottom: 1.5rem;
}

.image__img {
  display: block;
  width: 100%;
  -o-object-fit: cover;
     object-fit: cover;
  /* 100vh - (header padding, header text, metadata text, metadata padding */
